html {
  text-align: left;
  font-family: 'Ubuntu', sans-serif;
  color: rgb(92, 92, 92);
  background-color: #FBFBF2;
  max-width: 100%;
}

main {
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
}


h1, h2, h3, h4, h5 {
  color: #070707;
}

p {
  text-align: left;
  font-size: medium;
}

p, h1, h2, h3, h4, h5, a, ul {
  margin-left: 10px;
  margin-right: 10px;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
  max-width: 550px;
}

.project>p {
  padding-top: 7px;
}

header {
  /* background-color: #E5E6E4; */
  color: #2E3532;
  position: fixed;
  top: 0;
  width: 100%;
  height: 5.5%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  /* border-bottom: 5px solid maroon; */
}

header>button {
  background-color: #61dafb;
  border-color: #FBFBF2;
  border: none;
}

.contact {
  display: inline;
  text-align: left;
}

header>button {
  height: 100%;
}

.fa-compass {
  margin-right: 3px;
}

.fa-archway{
  padding-top: 33px;
}

.bio {  
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-align-content: flex-start;
          align-content: flex-start;
}

.bio>.flex {
  margin-top: 0px;
}

.bio>img {
  max-width: 150px;  
}

.App-header>button {
  margin: 0px;
}

img {
  max-width: 650px;
  min-width: 200px;
  height: auto;
}

/* @media only screen and (max-width: 900px) {
  .project>img {
    padding: 5px;
    max-width: 200px;
  }
} */

@media only screen and (max-width: 600px) {
  .flex-image {
    display: none;
  }
  .projects>section {
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .Single {
    margin-top: 15px;
  }
  footer>button {
    display: none;
  }
}

.flex-image {
  text-align: right;
}

.App-link {
  color: #61dafb;
}

.projects {
  padding-top: 20px;
  margin-top: 50px;
  /* color: #262626; */
  width: 100%;
  display: block;
  max-width: 100%;
}

.projects>h2 {
  font-size: 2em; 
  margin-top: 0.4em;
  margin-bottom: 10px;
}

.flex {
  -webkit-flex: auto;
          flex: auto;
}

@media only screen and (min-width: 601px) {
  .flex {
    max-width: 50%;
  }
  html {
    margin-left: 15px;
    margin-right: 15px;
  }
}

.project {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-align-content: center;
          align-content: center;
  margin-top: 4em;
  margin-bottom: 12em;
  max-width: 100%;
}
.headshot {
  max-width: 300px;
  height: auto;
  margin-top: 10px;
}

.flex-image-bio {
  text-align: center;
}

.flex>img {
  width: 100%;
  height: auto;
}

h3 {
  margin: 7px;
}

a {
  text-align: left;
  display: block;
  padding: 5px;
  color: #000060;
}

.test-credentails {
  margin-bottom: 17px;
}

.test-credentails>h3 {
  margin-bottom: 15px;
}

.test-credentails>p {
  -webkit-margin-before: 0em;
          margin-block-start: 0em;
  -webkit-margin-after: 0em;
          margin-block-end: 0em;
}

ul {
  list-style-type: none;
  text-align: left;
  max-width: 540px;
}

.past-app *{
  font-family: 'Times New Roman', Times, serif;
  color: white;
  background-image: url(/jack-eichner-portfolio/static/media/water014.4f146a46.gif);
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.time-machine {
  background-color: #070707
}

button {
  background-color: #fc5130;
  color: black;
  width: 200px;
  max-width: 200px;;
  margin-bottom: 15px;
  border: 3px solid #fc5130;
}

button:hover {
  background-color: black;
  color: #fc5130;
}

footer {
  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;
  display: -webkit-flex;
  display: flex;
}

footer>.contact {
  max-width: 1300px;
  display: -webkit-flex;
  display: flex;
  height: 20px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: baseline;
          align-items: baseline;
}

footer * h2 {
  display: none;
}

footer * a, button {
  -webkit-flex: auto;
          flex: auto;
}



